import React from "react";
import AppProvider from "./contexts";
import DefaultMyTravelLogin from "./DefaultMyTravelLogin";
import ModalMyTravelLogin from "./ModalMyTravelLogin";
import { I18nProvider, LOCALES } from "./shared/i18n";

interface IMyTravelLoginApp {
    isModal?: boolean;
    onCloseClick?(): void;
}

const MyTravelLoginApp: React.FC<IMyTravelLoginApp> = ({
    isModal,
    onCloseClick,
}: IMyTravelLoginApp) => {

    return (
        <I18nProvider locale={LOCALES["pt-BR"]}>
            <AppProvider isModal={isModal || false} onCloseClick={onCloseClick}>
                {isModal ?
                    <ModalMyTravelLogin onCloseClick={onCloseClick} /> : <DefaultMyTravelLogin />
                }
            </AppProvider>
        </I18nProvider>
    );
};

export default MyTravelLoginApp;
